@font-face {
  font-family: "Montserrat";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Light.woff2") format("woff2"), url("../fonts/Montserrat-Light.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.address {
  display: flex;
  align-items: center;
  font-style: normal;
}
@media (max-width: 767px) {
  .address {
    flex-direction: column;
  }
}

.address__phone {
  padding: 5px;
  padding-left: 30px;
  font-size: 15px;
  font-weight: 600;
  color: #FFFFFF;
  text-decoration: none;
  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
  background-position: 5px center;
}
@media (max-width: 767px) {
  .address__phone {
    margin-bottom: 20px;
  }
}

.address__text {
  margin: 0;
  padding: 5px;
  padding-left: 30px;
  font-size: 13px;
  background-image: url("../img/location.svg");
  background-repeat: no-repeat;
  background-position: 5px center;
}

.advantages {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .advantages {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 13px;
  }
}

.advantages__item {
  display: grid;
  grid-template-columns: 28px auto;
  column-gap: 5px;
  align-items: center;
}
.advantages__item::before {
  content: "";
  display: block;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: #00CABF;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (max-width: 767px) {
  .advantages__item + .advantages__item {
    margin-top: 13px;
  }
}

.advantages__item--pricetag::before {
  background-image: url("../img/ion_pricetags-outline.svg");
}

.advantages__item--shield::before {
  background-image: url("../img/shield.svg");
}

.advantages__item--gift::before {
  background-image: url("../img/gift.svg");
}

.advantages__item--wallet::before {
  background-image: url("../img/wallet.svg");
}

.advantages__item--tires::before {
  background-image: url("../img/tires.svg");
}

.button {
  padding: 11px;
  display: inline-block;
  color: #FFFFFF;
  background-color: #00CABF;
  border: none;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .button {
    padding: 21px;
    letter-spacing: 2px;
  }
}
.button:hover {
  background-color: #FFFFFF;
  color: #00CABF;
}

.button-large {
  font-size: 14px;
  font-weight: 500;
  padding: 10px;
}
@media (min-width: 768px) {
  .button-large {
    padding: 20px;
    font-size: 16px;
    min-width: 374px;
  }
}

.button-black {
  background-color: #000000;
}

.button-grey {
  background-color: #F3F3F3;
  border: 1px solid #000000;
  color: #000000;
}

.card {
  position: relative;
  padding: 40px;
  border: 3px solid #F3F3F3;
  background-color: #F3F3F3;
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-image: url("../img/Mask group.png"), linear-gradient(#FFFFFF 0, #FFFFFF 360px, #F3F3F3 360px);
  display: grid;
  row-gap: 10px;
  grid-template-rows: auto 300px auto min-content min-content min-content;
  grid-template-areas: "heading" "pictiure" "advantages" "btn-blue" "btn-dark" "btn-light";
}
@media (max-width: 767px) {
  .card {
    grid-template-rows: auto 230px auto min-content min-content min-content;
    padding: 10px;
    padding-top: 40px;
  }
}

.card__heading {
  display: grid;
  grid-template-rows: min-content min-content min-content;
}
@media (min-width: 767px) {
  .card__heading {
    grid-template-columns: 2fr 1fr;
    gap: 10px;
  }
}

.card__toggles {
  display: flex;
  justify-content: center;
  margin: 0;
  margin-top: 10px;
  padding: 10px 0;
}

.card__header {
  margin: 0;
  font-size: 22px;
  line-height: 30px;
  font-weight: 500;
}

.card__sign-up {
  display: grid;
  grid-template-columns: 28px auto;
  column-gap: 5px;
  align-items: center;
  font-size: 12px;
  font-weight: 300;
  color: #000000;
  text-decoration: none;
}
.card__sign-up::before {
  content: "";
  display: block;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: #00CABF;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url("../img/pen.svg");
}
.card__sign-up:hover {
  color: #00CABF;
}

.card__discount {
  margin: 0;
  font-size: 20px;
  grid-column: 1/-1;
}

.card__discount b {
  color: #00CABF;
}

.card__in-stock {
  position: absolute;
  top: -20px;
  right: 40px;
  margin: 0;
}

.cars {
  padding: 78px 0;
}

@media (min-width: 768px) {
  .cars__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 50px;
  }
}

@media (max-width: 767px) {
  .cars__item + .cars__item {
    margin-top: 50px;
  }
}

.container {
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .container {
    width: 280px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 1220px;
    padding-left: 40px;
    padding-right: 40px;
  }
}

.logo {
  display: block;
}

.logo__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .logo__wrapper {
    width: 307px;
  }
}

.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    flex-direction: column;
    padding: 0;
  }
}

.main-nav__menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}
@media (max-width: 767px) {
  .main-nav__menu {
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .main-nav__toggler {
    position: absolute;
    top: 60px;
    right: 0;
    z-index: 2;
    display: block;
    width: 70px;
    height: 40px;
    background-color: #FFFFFF;
    cursor: pointer;
    background-position: center;
    background-repeat: no-repeat;
    border: none;
  }
  .main-nav__toggler:hover, .main-nav__toggler:active {
    opacity: 80%;
  }
}
@media (min-width: 768px) {
  .main-nav__toggler {
    display: none;
  }
}

.main-nav--closed .main-nav__toggler {
  background-image: url("../img/burger.svg");
}

.main-nav--opened .main-nav__toggler {
  background-image: url("../img/close.svg");
}

@media (max-width: 767px) {
  .main-nav--closed .menu__item {
    display: none;
  }
  .main-nav--closed .menu__item--active {
    display: block;
  }
}
.main-nav--closed .menu__item--active a {
  text-align: center;
}

.main-nav--nojs .main-nav__toggler {
  display: none;
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-menu {
    display: block;
  }
  .main-nav--nojs .site-menu__item--active a {
    background-color: #00CABF;
    text-align: right;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 60px;
    right: 70px;
    left: 0;
    background-color: #000000;
    z-index: 10;
    height: 80vh;
  }
}
.menu__link {
  display: block;
  padding: 4px 7px;
  color: #FFFFFF;
  font-size: 11px;
  text-decoration: none;
}
@media (max-width: 767px) {
  .menu__link {
    text-transform: uppercase;
    padding: 10px;
    border-bottom: 1px solid grey;
  }
}

.menu__item:not(:last-child) {
  margin-right: 10px;
}

.service-menu .menu__link {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .menu__item--active {
    position: relative;
  }
  .menu__item--active::after {
    content: "";
    position: absolute;
    bottom: 4px;
    right: 7px;
    left: 7px;
    height: 2px;
    background-color: #FFFFFF;
  }
}

.menu__item:hover .menu__link {
  color: #00CABF;
}

.in-stock {
  display: inline-block;
  min-height: 44px;
  width: 190px;
  padding: 7px 20px;
  background-color: #00CABF;
  font-size: 12px;
  font-weight: 500;
  color: #FFFFFF;
  text-align: center;
}

.in-stock__count {
  color: #000000;
  display: block;
}

.option__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.option__toggles {
  margin: 0;
}

.swiper-pagination-bullet {
  width: 28px !important;
  height: 28px !important;
  border-radius: 50% !important;
  border: none !important;
  background-color: #00cabf !important;
}

.swiper-slide {
  margin: inherit;
}

.page-header {
  background-color: #000000;
  color: #FFFFFF;
}

.page-header__wrapper {
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .page-header__wrapper {
    width: 280px;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    max-width: 1220px;
  }
}

.page-header__bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px;
}
@media (max-width: 767px) {
  .page-header__bar {
    flex-direction: column;
  }
}

@media (min-width: 768px) {
  .page-header__address {
    width: 50%;
    justify-content: space-between;
  }
}

main {
  overflow: hidden;
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: normal;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: normal;
  }
}

.page__body {
  min-height: 100%;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

.promo {
  padding: 50px 0 50px;
  color: #FFFFFF;
  background-color: #000000;
  background-image: url("../img/promo-bgr.jpg");
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 10% 50%;
  text-align: center;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bgr@2x.jpg");
  }
}
@media (min-width: 768px) {
  .promo {
    padding: 63px 0 545px;
    background-size: cover;
  }
}

.promo__heading {
  margin: 0;
  margin-bottom: 70px;
  font-size: 24px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .promo__heading {
    font-size: 40px;
    line-height: 55px;
    margin-bottom: 24px;
  }
}

.promo__heading span {
  color: #00CABF;
}

.gallery {
  min-height: 756px;
  width: 100%;
}

.gallery__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.gallery {
  position: relative;
}

.slider__image {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -10;
}

.popup {
  width: 280px;
  text-align: center;
}
@media (min-width: 768px) {
  .popup {
    width: 350px;
  }
}

.popup__label {
  display: block;
  font-size: 20px;
  font-weight: 500;
  color: #00CABF;
  margin-bottom: 50px;
}

.popup__field {
  margin-bottom: 30px;
  padding: 10px 10px;
  border: 1px solid grey;
}

.popup__button {
  cursor: pointer;
}
.popup__button:hover {
  background-color: #F3F3F3;
  color: #000000;
}

.timer {
  padding: 50px 0 50px;
  background-color: #F3F3F3;
  color: #000000;
}

.timer__container {
  display: grid;
  grid-template-rows: 50% 50%;
  gap: 30px;
  align-items: center;
  overflow: hidden;
}
@media (min-width: 768px) {
  .timer__container {
    grid-template-columns: 50% 50%;
  }
}

.timer__header {
  margin: 0;
  margin-bottom: 15px;
  padding-bottom: 3px;
  font-size: 16px;
  text-transform: uppercase;
}

.swiper__wrapper {
  overflow: hidden;
}

.timer__button {
  padding: 25px;
  width: 100%;
}

.timer__grid {
  grid-row: 1/-1;
}